export const validators = {
    newsletterForm: values => {
        const errors = {}
        if (!values.password) {
            errors.password = 'Haso jest wymagane'
        } else if (values.password.length < 6) {
            errors.password = 'Haso powinno mie wicej ni 6 znakw'
        } else if (!/\d/.test(values.password)) {
            errors.password = 'Dla bezpieczestwa dodaj do hasa przynajmniej jedn cyfr'
        }
        if (values.clientType === 'personal') {
            if (!values.firstname) {
                errors.firstname = 'Imi jest wymagane'
            } else if (values.firstname.length < 2) {
                errors.firstname = 'Imi jest za krtkie'
            }
            if (!values.lastname) {
                errors.lastname = 'Nazwisko jest wymagane'
            } else if (values.lastname.length < 2) {
                errors.lastname = 'Nazwisko jest za krtkie'
            }
        } else if (values.clientType === 'company') {
            if (!values.companyName) {
                errors.companyName = 'Nazwa firmy jest wymagana w przypadku rejestracji jako firma'
            }
            if (!values.nip) {
                errors.nip = 'NIP jest wymagany w przypadku rejestracji jako firma'
            } else if (values.nip.length !== 10) {
                errors.nip = 'NIP jest nieprawidowy. Wpisz go bez spacji i mylnikw'
            }
        }
        if (!values.address) {
            errors.address = 'Adres jest wymagany. Podaj ulic i numer domu/bloku'
        }
        if (!values.postalCode) {
            errors.postalCode = 'Kod pocztowy jest wymagany'
        }
        if (!values.city) {
            errors.city = 'Miasto jest wymagane'
        }
        if (!values.email) {
            errors.email = 'Adres e-mail jest wymagany';
        } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)) {
            errors.email = 'Nieprawidowy adres e-mail'
        }
        if (!values.allowRodo) {
            errors.allowRodo = 'Musisz zaakceptowa regulamin RODO'
        }
        if (!values.allowTerms) {
            errors.allowTerms = 'Musisz zaakceptowa tre regulaminu korkw'
        }
        return errors
    }
}
